import React, {useEffect, useState} from "react";
import {API, graphqlOperation} from "aws-amplify";
import {listContents} from "../../graphql/queries";

const name = 'abc';
const flag = true;
const getAge = () =>{
    return 'test'
}

class HelloComponent extends React.Component {
    render() {
        return (
            <p>Hello Component</p>
        );
    }
}


 function Dashboard() {
    const [currentUser, setCurrentUser] = useState(name)
    const [contents, setContents] = useState([])
    const [loading, setLoading] = useState(false);

     function changeCurrentUser() {
        setCurrentUser('alvindaiyan2')
    }

    useEffect(() => {
        const loadData = async ()=> {
            setLoading(true)
            const contentsResp = await API.graphql(graphqlOperation(listContents));
            const contentArr = contentsResp.data.listContents
            setContents(contentArr)
            setLoading(false)
        }
        loadData();

        console.log(1)
    }, [])
    return (
        <div className="App">
            {currentUser} x {name} - {getAge()} {flag ? '-' + name : '' }
            <ul>
                {contents.map(content => <li key={content.id}>{content.id} : {content.content}</li>)}
            </ul>
            <HelloComponent></HelloComponent>
            <button onClick={changeCurrentUser}>My Button</button>
        </div>
    )
}

export default Dashboard;